import { createAction, handleActions } from 'redux-actions';
import { takeLatest } from 'redux-saga/effects';
import produce from 'immer';
import * as authAPI from '../api/search';
import createRequestSaga, {
    createRequestActionTypes
} from '../createRequestSaga';

const CHANGE_FIELD = 'auth/CHANGE_FIELD';
const INITIALIZE_FORM = 'auth/INITIALIZE_FORM';
const [SEARCH, SEARCH_SUCCESS, SEARCH_FAILURE] = createRequestActionTypes(
    'auth/SEARCH'
);

export const changeField = createAction(
    CHANGE_FIELD,
    ({ form, key, value }) => ({
        form, // search
        key, // keyword
        value // 실제 바꾸려는 값
    })
);

export const initializeForm = createAction(INITIALIZE_FORM, form => form); // register / login

export const search = createAction(SEARCH, keyword => keyword);

const keywordSaga = createRequestSaga(SEARCH, authAPI.search);

export function* searchSaga() {
    yield takeLatest(SEARCH, keywordSaga);
}

const initialState = {
    search: {
        keyword: ''
    },
    searchError: null,
    result: null


};

const searchReducer = handleActions(
    {
        [CHANGE_FIELD]: (state, { payload: { form, key, value } }) =>
            produce(state, draft => {
                draft[form][key] = value; 
            }),
        [INITIALIZE_FORM]: (state, { payload: form }) => ({
            ...state,
            [form]: initialState[form],
            search: null // 폼 전환 시 회원 인증 에러 초기화
        }),
        // 로그인 성공
        [SEARCH_SUCCESS]: (state, { payload: result }) => ({
            ...state,
            search: null,
            result
        }),
        // 로그인 실패
        [SEARCH_FAILURE]: (state, { payload: error }) => ({
            ...state,
            search: error
        })
    },
    initialState
);

export default search;