import React from "react";
import Container from "@material-ui/core/Container";
import { Link } from "react-router-dom";
import { makeStyles } from "@material-ui/core";

import rice from "../images/rice.png";
import sushi from "../images/sushi.png";
import steak from "../images/steak.png";
import chicken from "../images/chicken.png";
import jajang from "../images/jajang.png";
import hamburger from "../images/hamburger.png";
import pizza from "../images/pizza.png";
import moon from "../images/moon.png";
import soup from "../images/soup.png";
import lunchbox from "../images/lunchbox.png";
import dessert from "../images/dessert.png";
import kimbap from "../images/kimbap.png";

const useStyles = makeStyles((theme) => ({
  container: {
    display: "block",
    backgroundColor: "#ffffff",
    height: "100%",
    width: "100%",
    border: "1px solid rgba(0, 0, 0, .2)",
    borderRadius: " 10px 10px 10px 10px",
    marginBottom: "200px",
    paddingBottom: "30px",
  },
  picture: {
    width: "15%",
    height: "60px",
    marginTop: "40px",
    marginLeft: "15px",
    marginRight: "15px",
  },
}));

function MenuPage() {
  const classes = useStyles();

  return (
    <div>
      <Container>
        <Container className={classes.container} component="div">
          <a href="/">
            <img src={rice} alt="rice" className={classes.picture}></img>
          </a>
          <img src={steak} alt="steak" className={classes.picture}></img>
          <img src={sushi} alt="sushi" className={classes.picture}></img>
          <img src={kimbap} alt="kimbap" className={classes.picture}></img>
          <img src={jajang} alt="jajang" className={classes.picture}></img>
          <img src={chicken} alt="chicken" className={classes.picture}></img>
          <img src={pizza} alt="pizza" className={classes.picture}></img>
          <img
            src={hamburger}
            alt="hamburger"
            className={classes.picture}
          ></img>
          <img src={moon} alt="moon" className={classes.picture} />
          <img src={soup} alt="soup" className={classes.picture} />
          <img src={lunchbox} alt="lunchbox" className={classes.picture} />
          <img src={dessert} alt="dessert" className={classes.picture} />
        </Container>
      </Container>
    </div>
  );
}

export default MenuPage;
